* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    margin: 0 auto;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #f5f5f5;
    max-width: 550px;
    width: 550px;
}

label,
button {
    cursor: pointer;
}

main {
    margin: 130px 0px 40px 0;
    background-color: #fff;
    position: relative;
    box-shadow: 0 2px 4px 0 rgb(0 0 0 / 20%), 0 25px 50px 0 rgb(0 0 0 / 10%);
}

header h1 {
    position: absolute;
    top: -132px;
    width: 100%;
    font-size: 100px;
    font-weight: 400;
    text-align: center;
    color: rgba(175, 47, 47, 0.15);
    z-index: -1;
}

header div {
    padding: 16px 16px 16px 0;
    display: flex;
    width: 100%;
}

header div input {
    border: 0;
    outline: 0;
    background-color: transparent;
    line-height: 1.4em;
    font-size: 24px;
    width: 90%;
}

header div input::placeholder {
    color: #e6e6e6;
    font-style: italic;
}

.allCheckBtn {
    width: 10%;
    border: 0;
    background-color: transparent;
    color: #e6e6e6;
    font-size: 22px;
    font-weight: bold;
}

.allCheckBtn.allChecked {
    color: #737373;
}

ul {
    border-top: 1px solid #e6e6e6;
    list-style: none;
    width: 100%;
}

ul li {
    border-bottom: 1px solid #ededed;
    position: relative;
    font-size: 24px;
    padding: 15px;
    display: flex;
    width: 100%;
    justify-content: space-between;
}

ul li input {
    display: none;
}

ul li input:checked + label {
    text-align: center;
    line-height: 40px;
    border-color: #cee4e0;
}

ul li input:checked + label:before {
    content: "âœ”";
    color: #61c4b1;
}

ul li label {
    position: relative;
    width: 40px;
    height: 40px;
    border: 2px solid #ededed;
    border-radius: 100%;
}

ul li:hover button {
    visibility: visible;
}

ul li span {
    width: 80%;
    word-break: break-all;
    line-height: 40px;
    outline: 0;
}

ul li button {
    outline: 0;
    border: 0;
    background-color: transparent;
    padding: 5px;
    visibility: hidden;
}

.select {
    border: 1px solid #000;
}

footer {
    display: flex;
    position: relative;
    justify-content: space-between;
    padding: 5px 15px;
    color: #777;
}

footer:before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 50px;
    overflow: hidden;
    box-shadow: 0 1px 1px rgb(0 0 0 / 20%), 0 8px 0 -3px #f6f6f6,
        0 9px 1px -3px rgb(0 0 0 / 20%), 0 16px 0 -6px #f6f6f6,
        0 17px 2px -6px rgb(0 0 0 / 20%);
    z-index: 1;
}

footer > span {
    z-index: 2;
    line-height: 35px;
}

footer .menu {
    display: flex;
    justify-content: center;
    z-index: 2;
}

footer input {
    display: none;
}

footer input:checked + label {
    border-color: rgba(175, 47, 47, 0.2);
}

footer label {
    margin: 3px;
    padding: 3px 7px;
    border: 1px solid transparent;
    border-radius: 3px;
}

footer label:hover {
    border-color: rgba(175, 47, 47, 0.1);
}

footer label p {
    text-transform: capitalize;
}

footer > button {
    z-index: 2;
    background-color: transparent;
    border: 0;
    outline: 0;
    color: #777;
}

footer > button:hover {
    text-decoration: underline;
}

.copyright {
    text-align: center;
    color: #bfbfbf;
    font-size: 10px;
    line-height: 2;
    margin-bottom: 20px;
}

.copyright a {
    color: #bfbfbf;
}

@keyframes moving-ari {
    0% {
        top: 0%;
        left: 0%;
    }
    25% {
        top: 0%;
        left: calc(100% - 50px);
    }
    50% {
        top: calc(100% - 50px);
        left: calc(100% - 50px);
    }
    75% {
        top: calc(100% - 50px);
        left: 0%;
    }
    100% {
        top: 0%;
        left: 0%;
    }
}

#cursorImg {
    width: 50px;
    height: 50px;
    border-radius: 100%;
    cursor: pointer;
    position: absolute;
    top: 0%;
    left: 0%;
    z-index: -2;
    animation: moving-ari 40s infinite;
    transition: opacity 1s;
}

.bottom-sheet {
    position: fixed;
    right: 10px;
    bottom: 0;
    transition: all 1s;
    border: 0;
    width: 20%;
    background-color: white;
    box-shadow: 0 2px 4px 0 rgb(0 0 0 / 20%), 0 25px 50px 0 rgb(0 0 0 / 10%);
    border-radius: 10px 10px 0px 0px;
    transform: translateY(calc(100% - 41.6px));
}

.bottom-sheet.show {
    transform: translateY(0);
}

.bottom-sheet > div {
}

.bottom-sheet > div input {
    display: none;
}

.bottom-sheet > div label {
    display: block;
    width: 100%;
    height: 100%;
    padding: 10px;
}

.bottom-sheet ul {
    max-height: 314px;
    overflow-y: scroll;
}

.nothing {
    background-color: #ececec;
}
